import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import {
  getDataBaki,
  getDataJenis,
} from "../../../../actions/datamaster_action";
import LaporanDetailBarangExel from "./exel/LaporanTambahBarangExel";
import Tabel from "../../../../components/helpers/tabel";
import CetakLaporanTambahBarang from "./pdf/CetakLaporanTambahBarang";
import Loading from "react-fullscreen-loading";
import {
  InputDate,
  ReanderSelect,
  SkeletonLoading,
} from "../../../../components/helpers/field";
import {
  NotifError,
  ToastNotification,
} from "../../../../components/helpers/notification";
import {
  formatDate,
  getTglSystem,
  localStorageencryp,
} from "../../../../components/helpers/function";
import moment from "moment";
import { getData } from "../../../../config/axios";
import CetakLaporanTambahBarangGlobalBaki from "./pdf/CetakLaporanTambahBarangGlobalBaki";
import LaporanTambahBarangExelGlobalBaki from "./exel/LaporanTambahBarangExelGlobalBaki";
import LaporanTambahBarangExelGlobalJenis from "./exel/LaporanTambahBarangExelGlobalJenis";
import CetakLaporanTambahBarangGlobalJenis from "./pdf/CetakLaporanTambahBarangGlobalJenis";

class HeadFilterLaporan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      data_per_baki: [],
      data_per_jenis: [],
      table_per_baki: false,
      table_per_jenis: false,
      isLoading: false,
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  componentDidMount() {
    this.props.dispatch(getDataBaki());
    this.props.dispatch(getDataJenis());
    this.props.change('kode_toko',"ALL")
    this.props.change('kode_dept', "ALL")
    this.props.change("tgl_awal",getTglSystem())
    this.props.change("tgl_akhir",getTglSystem())
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    if (new Date(date) > new Date()) {
      ToastNotification("info", "Tanggal tidak boleh lebih dari hari ini");
      return false;
    }
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  exportPdf() {
    CetakLaporanTambahBarang(this.props.data);
    this.props.change("kode_toko", "ALL");
    this.props.change("kode_dept", "ALL");
  }

  exportPdfGlobalBaki() {
    CetakLaporanTambahBarangGlobalBaki(this.state.data_per_baki);
  }

  exportPdfGlobalJenis() {
    CetakLaporanTambahBarangGlobalJenis(this.state.data_per_jenis);
  }

  GlobalPerBaki() {
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(this.props.tgl_awal || getTglSystem())).format(
          "YYYY-MM-DD"
        )
      ),
      tgl_akhir: formatDate(
        moment(new Date(this.props.tgl_akhir || getTglSystem())).format(
          "YYYY-MM-DD"
        )
      ),
    };
    this.setState({ isLoading: true });
    getData(
      `barang/tambah-barangs/global-per-baki?tanggal[gte]=${data_post.tgl_awal}&tanggal[lte]=${data_post.tgl_akhir}`
    )
      .then((res) => {
        if (res.data.length === 0) {
          this.setState({
            export: false,
            LaporanBarangDetail: [],
            isLoading: false,
          });
          ToastNotification("info", "Laporan Tambah Barang Tidak Tersedia");
        } else {
          ToastNotification("success", "Laporan Tambah Barang Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));

          this.setState({
            isLoading: false,
            export: true,
            data_per_baki: res.data,
            table_per_baki: true,
            table_per_jenis: false,
            table_per_normal: false,
          });
        }
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .catch((error) => this.errorHandling(error));
  }

  GlobalPerJenis() {
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(this.props.tgl_awal || getTglSystem())).format(
          "YYYY-MM-DD"
        )
      ),
      tgl_akhir: formatDate(
        moment(new Date(this.props.tgl_akhir || getTglSystem())).format(
          "YYYY-MM-DD"
        )
      ),
    };
    this.setState({ isLoading: true });
    getData(
      `barang/tambah-barangs/global-per-jenis?tanggal[gte]=${data_post.tgl_awal}&tanggal[lte]=${data_post.tgl_akhir}`
    )
      .then((res) => {
        if (res.data.length === 0) {
          this.setState({
            export: false,
            LaporanBarangDetail: [],
            isLoading: false,
          });
          ToastNotification("info", "Laporan Tambah Barang Tidak Tersedia");
        } else {
          ToastNotification("success", "Laporan Tambah Barang Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));

          this.setState({
            isLoading: false,
            export: true,
            data_per_jenis: res.data,
            table_per_jenis: true,
            table_per_baki: false,
          });
        }
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .catch((error) => this.errorHandling(error));
  }

  errorHandling(err) {
    NotifError(err);
    this.setState({ isLoading: false });
  }

  render() {
    let databaki = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.databaki.forEach((list) => {
      let data = {
        value: list.kode_baki,
        name: list.kode_baki + " - " + list.nama_baki,
      };
      databaki.push(data);
    });
    let dataJenis = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.dataJenis.forEach((list) => {
      let data = {
        value: list.kode_dept,
        name: list.kode_dept + " - " + list.nama_dept,
      };
      dataJenis.push(data);
    });
    return (
      <>
        <form
          onSubmit={(data) => this.props.handleSubmit(data)}
          autoComplete="off"
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
        >
          <div className="row">
            <div className="col-lg-3">
              <Field
                name="tgl_awal"
                component={InputDate}
                label="Tanggal Dari"
                type="text"
                defaultValue={getTglSystem()}
                selected={this.state.tgl_awal}
                onChange={(date) => this.setStartDate(date)}
                placeholder="Masukan Tanggal Dari"
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="tgl_akhir"
                component={InputDate}
                type="text"
                defaultValue={getTglSystem()}
                selected={this.state.tgl_akhir}
                onChange={(date) => this.setLastDate(date)}
                label="Tanggal Akhir"
                placeholder="Masukan Tanggal Akhir"
              />
            </div>
            <div className="col-3">
              {this.state.loading === false ? (
                <Field
                  label="Jenis"
                  name="kode_dept"
                  options={dataJenis}
                  placeholder={"Silahkan Pilih Kode Jenis"}
                  component={ReanderSelect}
                />
              ) : (
                <SkeletonLoading label="Kode Jenis" />
              )}
            </div>
            <div className="col-3">
              {this.state.loading === false ? (
                <Field
                  label="Baki"
                  name="kode_toko"
                  options={databaki}
                  placeholder={"Silahkan Pilih Kode Baki"}
                  component={ReanderSelect}
                />
              ) : (
                <SkeletonLoading label="Kode Baki" />
              )}
            </div>
            <div className="col-4 mb-2">
              <label> &nbsp; </label>
              <button
                className="btn btn-primary btn-block"
                disabled={this.submitting}
                type="submit"
                onClick={() =>
                  this.setState({
                    table_per_normal: true,
                    table_per_baki: false,
                    table_per_jenis: false,
                  })
                }
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Memfilter
                  </>
                ) : (
                  "Lihat Laporan"
                )}
              </button>
            </div>
            <div className="col-4 mb-2">
              <label> &nbsp; </label>
              <button
                className="btn btn-primary btn-block"
                disabled={this.submitting}
                type="button"
                onClick={() => this.GlobalPerBaki()}
              >
                {this.state.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Memfilter
                  </>
                ) : (
                  "Global Per baki"
                )}
              </button>
            </div>
            <div className="col-4 mb-2">
              <label> &nbsp; </label>
              <button
                className="btn btn-primary btn-block"
                disabled={this.submitting}
                type="button"
                onClick={() => this.GlobalPerJenis()}
              >
                {this.state.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Memfilter
                  </>
                ) : (
                  "Global Per Jenis"
                )}
              </button>
            </div>
            <div className="col-12">
              {this.state.table_per_baki ? (
                <Tabel
                  keyField="kode_barcode"
                  data={this.state.data_per_baki}
                  columns={this.props.columns_per_baki}
                  empty={true}
                  textEmpty="Data Laporan Barang Global Per Baki Kosong"
                />
              ) : this.state.table_per_jenis ? (
                <Tabel
                  keyField="kode_barcode"
                  data={this.state.data_per_jenis}
                  columns={this.props.columns_per_jenis}
                  empty={true}
                  textEmpty="Data Laporan Barang Detail Kosong"
                />
              ) : (
                <Tabel
                  keyField="kode_barcode"
                  data={this.props.data}
                  columns={this.props.columns}
                  empty={true}
                  textEmpty="Data Laporan Barang Detail Kosong"
                />
              )}
            </div>
            {this.props.export ? (
              this.state.table_per_normal ? (
                <>
                  <div className="col-lg-6">
                    <button
                      type="button"
                      onClick={() => this.exportPdf()}
                      className="btn btn-warning btn-block"
                    >
                      {" "}
                      Export PDF{" "}
                    </button>
                  </div>
                  <div className="col-lg-6">
                    <LaporanDetailBarangExel
                      data={this.props.data}
                      kode_baki={this.props.kode_baki}
                    />
                  </div>
                </>
              ) : null
            ) : null}
            {this.state.table_per_baki ? (
              <>
                <div className="col-lg-6">
                  <button
                    type="button"
                    onClick={() => this.exportPdfGlobalBaki()}
                    className="btn btn-warning btn-block"
                  >
                    {" "}
                    Export PDF{" "}
                  </button>
                </div>
                <div className="col-lg-6">
                  <LaporanTambahBarangExelGlobalBaki
                    data={this.state.data_per_baki}
                    kode_baki={this.props.kode_baki}
                  />
                </div>
              </>
            ) : null}
            {this.state.table_per_jenis ? (
              <>
                <div className="col-lg-6">
                  <button
                    type="button"
                    onClick={() => this.exportPdfGlobalJenis()}
                    className="btn btn-warning btn-block"
                  >
                    {" "}
                    Export PDF{" "}
                  </button>
                </div>
                <div className="col-lg-6">
                  <LaporanTambahBarangExelGlobalJenis
                    data={this.state.data_per_jenis}
                    kode_baki={this.props.kode_baki}
                  />
                </div>
              </>
            ) : null}
          </div>
          {this.props.isLoading ? (
            <Loading
              loading
              background="rgba(0, 0, 0, 0.35)"
              loaderColor="rgba(94, 147, 117, 1)"
            />
          ) : null}
          {this.state.isLoading ? (
            <Loading
              loading
              background="rgba(0, 0, 0, 0.35)"
              loaderColor="rgba(94, 147, 117, 1)"
            />
          ) : null}
        </form>
      </>
    );
  }
}

