HeadFilterLaporan = reduxForm({
  form: "HeadFilterLaporan",
  enableReinitialize: true,
})(HeadFilterLaporan);
const selector = formValueSelector("HeadFilterLaporan"); // <-- same as form name
export default connect((state) => {
  return {
    databaki: state.datamaster.getDataBaki,
    dataJenis: state.datamaster.getDataJenis,
    tgl_awal: selector(state, "tgl_awal"),
    tgl_akhir: selector(state, "tgl_akhir"),
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFilterLaporan);
