import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { formatGram, getUserdata } from "../../../../../components/helpers/function";

class LaporanTambahBarangExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
    render() {
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Tambah Barang"
          sheet="Laporan Tambah Barang"
          buttonText="Export Exel"
        />
        <table rowkey="kode_member" id="table-to-xls" style={{ display: "none" }}>
        <thead>
              <tr>
                <th colSpan="12" style={{textAlign : "center"}}> LAPORAN TAMBAH BARANG </th>
              </tr>
              <tr>
              <th colSpan="12"> TOKO :  {getUserdata(getUserdata).nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="12"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr></tr>
            <tr >
                <th  style={{ backgroundColor: "#E8E5E5", color: "#000"}}>NO</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>KODE BARCODE</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>KODE GUDANG</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>KODE TOKO</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>KODE GROUP</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>KODE JENIS</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>NAMA BARANG</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>NAMA ATRIBUT</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>BERAT</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>BERAT ASLI</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>BERAT ATRIBUT</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>HARGA JUAL</th>
                {/* <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>JUMLAH</th> */}
            </tr>
        </thead>
            
            <tbody>
            {this.props.data.map((item, index) => (
                    <tr key={index}>
                        <td style={{ textAlign : "center" }}> {index+1}</td>
                        <td>&nbsp;{item.kode_barcode}</td>
                        <td> {item.kode_gudang}</td>
                        <td> {item.kode_toko}</td>
                        <td> {item.kode_group}</td>
                        <td> {item.kode_dept}</td>
                        <td> {item.nama_barang}</td>
                        <td> {item.nama_atribut}</td>
                        <td style={{textAlign : "right"}}> {formatGram(item.berat,3)}</td>
                        <td style={{textAlign : "right"}}> {formatGram(item.berat_asli,3)}</td>
                        <td style={{textAlign : "right"}}> {formatGram(item.berat_atribut,3)}</td>
                        <td style={{textAlign : "right"}}> {item.harga_jual.toLocaleString("kr-KO")}</td>
                        {/* <td style={{ border: "1px solid #000" }}> {item.stock_on_hand}</td> */}
                    </tr>
             ))}
            </tbody>
                    
            <tfoot>
                <tr>
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "center" }} colSpan="8">  </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"}}> {formatGram(this.props.data.map(list => list.berat).reduce((a,b) => a+b,0),3)}   </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"}}> {formatGram(this.props.data.map(list => list.berat_asli).reduce((a,b) => a+b,0),3)}  </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"}}> {formatGram(this.props.data.map(list => list.berat_atribut).reduce((a,b) => a+b,0),3)}  </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"}}> {this.props.data.map(list => list.harga_jual).reduce((a,b) => a+b,0).toLocaleString("kr-KO")}  </th>    
                </tr>
            </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanTambahBarangExel;
