import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../../components/panel/panel.jsx";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import { formatDate, formatGram, getTglSystem, localStoragedecryp, localStorageencryp } from "../../../../components/helpers/function.jsx";
import "react-datepicker/dist/react-datepicker.css";
import {
  NotifError,
  ToastNotification,
} from "../../../../components/helpers/notification.jsx";
// import CetakLaporanDetailBarang from "./CetakLaporanDetailBarang.jsx";
// import FormFilter from "./FormFilter.jsx";
import {  postDataNoTImeOut } from "../../../../config/axios.jsx";
import HeadFilterLaporan from "./HeadFilterLaporan.jsx";
import moment from "moment";
// import LaporanDetailexel from "./LaporanDetailExel.jsx";
const maptostate = (state) => {
  return {
    hideModal: state.datamaster.modalDialog,
  };
};

class FormLaporanTambahBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      export: false,
      kode_baki: [],
      LaporanBarangDetail: false,
      columns: [
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
        },
        {
          dataField: "kode_gudang",
          text: "Kode Gudang",
        },

        {
          dataField: "kode_toko",
          text: "Kode Baki",
        },
     
        {
          dataField: "kode_group",
          text: "Kode Group",
        },
        {
          dataField: "kode_dept",
          text: "Kode Jenis",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "nama_atribut",
          text: "Nama Atribut",
        },
        // {
        //   dataField: "berat_tambah",
        //   text: "Berat Tambah",
        //   headerClasses: 'text-right',
        //   formatter: (cell) => {
        //     return <div className="text-right"> {formatGram(cell, 3)} </div>;
        //   },
        // },
        {
          dataField: "berat",
          text: "Berat",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
        {
          dataField: "berat_asli",
          text: "Berat Asli",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
       
        {
          dataField: "berat_atribut",
          text: "Berat Atribut",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
        {
          dataField: "harga_jual",
          text: "Harga Jual",
          headerClasses: 'text-right',
          formatter: (data) => {
            return <div className="text-right"> {parseFloat(data).toLocaleString("kr-KO")}</div>;
          },
        },
      ],
      columns_per_baki : [
        {
          dataField: "kode_toko",
          text: "Kode Baki",
        },
        {
          dataField: "nama_toko",
          text: "Nama Baki",
        },
        {
          dataField: "stock_tambah",
          text: "Tambah Ptg",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {cell} </div>;
          },
        },
        {
          dataField: "berat_tambah",
          text: "Tambah Berat",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
        {
          dataField: "berat_asli_tambah",
          text: "Tambah Berat Asli",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },

      ],
      columns_per_jenis : [
        {
          dataField: "kode_dept",
          text: "Kode Jenis",
        },
        {
          dataField: "nama_dept",
          text: "Nama Jenis",
        },
        {
          dataField: "stock_tambah",
          text: "Tambah Ptg",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {cell} </div>;
          },
        },
        {
          dataField: "berat_tambah",
          text: "Tambah Berat",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
        {
          dataField: "berat_asli_tambah",
          text: "Tambah Berat Asli",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
  }
  handleSubmit(data) {
    let data_post = {
      tgl_awal: formatDate(moment(new Date(data.tgl_awal || getTglSystem())).format('YYYY-MM-DD')),
      tgl_akhir: formatDate(moment(new Date(data.tgl_akhir || getTglSystem())).format('YYYY-MM-DD')),
    };
    this.setState({ isLoading: true, kode_toko: data.kode_baki });
    postDataNoTImeOut(`barang/tambah-barangs?tanggal[gte]=${data_post.tgl_awal}&tanggal[lte]=${data_post.tgl_akhir}&kode_dept=${data.kode_dept}&kode_toko=${data.kode_toko}`)
      .then((res) => {
        if (res.data.length === 0) {
          this.setState({
            export: false,
            LaporanBarangDetail: [],
          });
          ToastNotification("info", "Laporan Tambah Barang Tidak Tersedia");
        } else {
          ToastNotification("success", "Laporan Tambah Barang Tersedia");
          localStorageencryp('tgl_laporan', JSON.stringify(data_post))
          this.setState({
            export: true,
            LaporanBarangDetail: res.data,
          });
        }
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .catch((error) => this.errorHandling(error));
  }
  errorHandling(err) {
    NotifError(err);
    this.setState({ isLoading: false });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan Tambah Barang</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Tambah Barang Detail</li>
        </ol>
        <h1 className="page-header">Laporan Tambah Barang</h1>
        <Panel>
          <PanelHeader>Filter Laporan</PanelHeader>
          <br />
          <div className="container">
            <div className="row">
              <div className="col-12">
                <HeadFilterLaporan
                  kode_baki={this.state.kode_baki}
                  export={this.state.export}
                  columns={this.state.columns}
                  data={this.state.LaporanBarangDetail}
                  isLoading={this.state.isLoading}
                  columns_per_baki={this.state.columns_per_baki}
                  columns_per_jenis ={this.state.columns_per_jenis}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
        
            </div>
          </div>
          <br />
        </Panel>
      </div>
    );
  }
}

FormLaporanTambahBarang = reduxForm({
  form: "FormLaporanTambahBarang",
  enableReinitialize: true,
})(FormLaporanTambahBarang);
export default connect(maptostate, null)(FormLaporanTambahBarang);
